
import XHeader from 'vux/src/components/x-header/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'

import axios from 'axios'
import { getItem } from '../../utils/storage'
import { userInfoTran } from '../../utils/dataTran'
export default {
components: {
XHeader,
Search,
Checklist
},
data () {
return {
results: [],
value: '',
chooseItem: '',
list: [],
chooseIds: [],
userId: getItem('user').id
}
},
mounted () {
this.getResult()
},
methods: {
change (value) {
this.chooseIds = value
},
getResult (val) {
axios
.get(`/api/getfriendslist`, {
params: {
userId: this.userId,
val: val || ''
}
})
.then((res) => {
const data = res.data.status && userInfoTran(res.data.list, false)
data.forEach((el, index) => {
this.list.push({
key: el.id,
value: el.name
})
})
}).catch((error) => {
console.log(error)
})
},
shareFile () {
const body = {
userId: this.userId,
fileId: this.fileId
}
axios
.post(`${global.IP}/sharefile`, body)
.then((res) => {
res.data && this.$router.push({ path: '/note/list' })
}).catch((error) => {
console.log(error)
})
}
}
}



