
import 'mavon-editor/dist/css/index.css'
import {mavonEditor} from 'mavon-editor'
import XInput from 'vux/src/components/x-input/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'

import axios from 'axios'
import { getItem } from '../../utils/storage'

export default {
data () {
return {
item: {},
options: [{
key: 0,
value: '工作文件'
}, {
key: 1,
value: '生活文件'
}],
show: false,
opt: 0,
userId: getItem('user').id
}
},
components: {
mavonEditor,
XInput,
XHeader,
PopupHeader,
Popup,
Group,
Radio
},
mounted () {
const { item } = this.$route.params
this.item = item || {
title: '',
content: '',
type: 0,
fileType: 'md',
fileId: ''
}
},
methods: {
saveFile () {
this.show = !this.show
const body = {
id: this.userId,
title: this.title === '' ? '无标题' : this.title,
...this.item
}
axios
.post(`/api/savefile`, body)
.then((res) => {
res.data && this.$router.push({ path: '/note/list' })
}).catch((error) => {
console.log(error)
})
}
}
}



